  var mm_path;

  mm_path = function() {
    var base_path, p;
    base_path = meta_model_root_path;
    if (base_path.slice(-1) === '/') {
      base_path = base_path.slice(0, -1);
    }
    p = path;
    if (p[0] === '/') {
      p = p.slice(1);
    }
    return base_path + '/' + p;
  };

