define('ember-src/tests/unit/models/property-test', ['ember-qunit'], function (ember_qunit) {

  'use strict';

  ember_qunit.moduleForModel('property', {
    needs: []
  });

  ember_qunit.test('it exists', function(assert) {
    var model;
    model = this.subject();
    return assert.ok(!!model);
  });

});